<!-- 资源平台 box -->
<template>
  <div class="res-box">
    <div class="res-bg">
      <div v-if="show  && info.specialId" class="play-time">{{info.resourceCount | formatCount}}</div>
      <div v-else class="play-time">{{info.resourceFileTime}}</div>
      <div class="play-icon">
        <i class="p-center iconfont icon-video"></i>
      </div>
    </div>
    <div class="content">
      <div class="title text-overflow">{{info.resourceName}}</div>
      <div v-if="show && info.specialMask" class="show">{{info.specialMask}}</div>
      <div class="info">
        <span>{{info.userName}}</span>
        <span class="watch-number">
          <i class="icon-eyes iconfont"></i>{{info.visitCount}}
        </span>
        <span>
          <i style="color:#FF8F1F" class="icon-solid-star iconfont"></i>{{info.avgScore}}
        </span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: {
    show: Boolean,
    info:{
      type:Object,
      default:function(){
        return {
          resourceFileTime:'12集',
          resourceName:'张三',
          userName:'lisi',
          visitCount:'12',
          avgScore:'2'
        }
      }
    }
  },
  filters:{
    formatCount(val) {
      return val + "集"
    }
  },
};
</script>
<style lang="scss" scoped>
.res-box {
  padding: 0 1%;
  margin-bottom: 20px;
  cursor: pointer;
  .res-bg {
    height: 0;
    position: relative;
    top: 0;
    border-radius: 8px;
    overflow: hidden;
    padding-bottom: 56%;
    background: url(./../assets/image/city-1.jpg) no-repeat;
    background-size: 100% 100%;
    transition: all 0.1s linear;
    .play-time {
      position: absolute;
      right: 10px;
      bottom: 10px;
      color: #fff;
      border-radius: 15px;
      padding: 1px 8px;
      background: rgba(0, 0, 0, 0.4);
    }
    .play-icon {
      display: none;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background: rgba(0, 0, 0, 0.02);
      i {
        color: #fff;
        font-size: 60px;
      }
    }
  }
  .res-bg:hover {
    top: -6px;
    .play-icon {
      display: block;
    }
  }
  .content {
    .title {
      font-size: 16px;
      color: #333333;
      line-height: 40px;
      cursor: pointer;
    }
    .title:hover {
      color: #4a80f6;
    }
    .show {
      color: #909399;
      font-size: 14px;
      line-height: 22px;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      margin-bottom: 0.5rem;
      &:hover {
        color: #4a80f6;
      }
    }
    .info {
      font-size: 14px;
      span {
        color: #8f9399;
        i {
          margin-right: 3px;
          color: inherit;
        }
      }
      .watch-number {
        margin: 0 10px;
      }
    }
  }
}
</style>